@import url("https://fonts.googleapis.com/css2?family=Poppins&family=Space+Grotesk:wght@500&display=swap");

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  display: flex;
  justify-content: center;
  margin-top: 60px;
  max-height: 100vh;
  box-sizing: border-box;
}
main {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 600px;
  height: 500px;
}
.gif {
  width: 100%;
  display: flex;
  justify-content: center;
}

#spiderman {
  width: 100%;
  height: 300px;
}

.btns {
  display: flex;
  justify-content: center;
  position: relative;
}

button {
  padding: 12px 15px;
  width: 150px;
  border: none;
  color: white;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
}

.yes {
  background-color: #03c03c;
  position: absolute;
  margin-right: 190px;
  margin-top: 1px;
}

.yes:hover {
  background-color: #00563b;
}

.no {
  background-color: red;
  position: absolute;
  margin-left: 15px;
  transition: 0.4s;
}

.no:hover {
  background-color: #7c0902;
}

@media screen and (max-width: 500px) {
  main {
    max-width: 300px;
  }
  .spiderman {
    width: 250px;
    height: 250px;
  }
  .us {
    width: 70%;
  }
  .text {
    font-size: 30px;
    font-weight: 500;
    text-align: center;
  }
  .ending-text {
    font-size: 25px;
    text-align: center;
    font-weight: 500;
  }
  button {
    padding: 5px 6px;
    max-width: 120px;
    text-wrap: nowrap;
  }
  .yes {
    margin-right: 140px;
  }
  .no {
    margin-left: 10px;
  }
}
